<script>
import $ from "jquery";
import jQuery from "jquery";
export default {
	name: "Home",
	mounted() {
		jQuery(document).ready(function ($) {
			$('.hero').slick({
				dots: false,
				infinite: true,
				speed: 500,
				fade: !0,
				cssEase: 'linear',
				slidesToShow: 1,
				slidesToScroll: 1,
				autoplay: true,
				autoplaySpeed: 8000,
				draggable: true,
				arrows: true,
				responsive: [
				{
				breakpoint: 1024,
				settings: {
				slidesToShow: 1,
				slidesToScroll: 1,
				infinite: true
						}
				},
				{
				breakpoint: 768,
				settings: {
				draggable: true,
						}
				},
				{
				breakpoint: 600,
				settings: {
				slidesToShow: 1,
				draggable: true,
				slidesToScroll: 1
						}
				},
				{
				breakpoint: 480,
				settings: {
				slidesToShow: 1,
				draggable: true,
				slidesToScroll: 1
						}
				}
			
						]
			});
		});

				// blog slider script
				$(".product-slider").slick({
					dots: false,
					infinite: true,
					speed: 1000,
					slidesToShow: 3,
					slidesToScroll: 3,
					autoplay: true,
					fade: false,
					responsive: [
						{
							breakpoint: 1024,
							settings: {
								slidesToShow: 3,
								slidesToScroll: 3,
								infinite: true,
								dots: false,
								autoplay: true,
							},
						},
						{
							breakpoint: 600,
							settings: {
								slidesToShow: 2,
								slidesToScroll: 2,
								infinite: true,
								dots: true,
								arrows: false,
								autoplay: true,
							},
						},
						{
							breakpoint: 480,
							settings: {
								slidesToShow: 1,
								slidesToScroll: 1,
								infinite: true,
								dots: true,
